import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"
import { getBalanceSheetFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function netCurrentAssetValueTtm(tableName: string) {
  const title = "Net Current Asset Value (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["netCurrentAssetValueTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Current assets minus liabilities.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            const fiscalPeriod = getBalanceSheetFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about net current asset value or other relevant financial measures?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.netCurrentAssetValueTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.netCurrentAssetValueTtm}
        />
      )
    },
  })
}

export { netCurrentAssetValueTtm }
